<template>
    <div>
        <div class="card bg-light mb-3">
            <div class="card-header bg-dark text-white">
                <b-row>
                    <b-col>
                        <div v-if="!loading">
                            <font-awesome-icon icon="database" /><strong>&nbsp;{{list.total}}&nbsp;</strong>
                            <small>Result/s found</small>
                        </div>
                        <div v-else>
                            <strong>&nbsp;Fetching result...&nbsp;</strong>
                        </div>
                    </b-col>
                    <b-col class="text-right">
                        <b-button variant="secondary" size="sm" @click="add_member_modal=true">
                            <font-awesome-icon icon="plus" /> Add Member
                        </b-button>
                    </b-col>
                </b-row>

            </div>
            <div class="card-body bg-white">
                <b-row class="mb-3">
                    <b-col sm="2">
                        <b-form-select v-model="selected_display_no" :options="options" size="sm" @change="changeDisplayNo"></b-form-select>
                    </b-col>
                    <b-col sm="6" offset-sm="4">
                        <b-input-group>
                            <b-input-group-prepend>
                                <b-input-group-text class="bg-dark text-white"><font-awesome-icon icon="search" /></b-input-group-text>
                            </b-input-group-prepend>
                            <b-form-input
                                    type="text"
                                    class="form-control"
                                    placeholder="Search"
                                    autocomplete="off"
                                    size="sm"
                                    @keypress.native="searching"
                                    @keyup.native="search"
                                    debounce="500"
                                    v-model="search_term"
                            />
                            <b-input-group-append>
                                <b-button @click="clearSearch" variant="dark" class="bg-dark text-white px-4" size="sm" :disabled="search_term==''">
                                    <font-awesome-icon icon="times" />
                                </b-button>
                                <!--<i class="fa fa-close"></i>-->
                            </b-input-group-append>
                        </b-input-group>
                    </b-col>
                </b-row>
                <b-table
                        selectable
                        selectedVariant="info"
                        select-mode="single"
                        :dark="dark"
                        :hover="hover"
                        :striped="striped"
                        :bordered="bordered"
                        :fixed="fixed"
                        responsive="sm"
                        :items="table_items"
                        :fields="captions"
                        :busy="loading"
                        show-empty
                        @row-dblclicked="rowDbClicked"
                        head-variant="dark"
                        class="cursor-pointer"
                >
                    <div class="text-center  my-2" slot="table-busy">
                        <br>
                        <b-spinner small class="align-middle"></b-spinner>
                        <br><br>
                        <strong>Loading</strong>
                    </div>
                    <template v-slot:cell(action)="data">
                        <div class="text-center">
                            <b-button
                                    variant="danger"
                                    size="sm"
                                    @click="removeMember(data.item.id)"
                                    class="mr-2"
                            >
                                <font-awesome-icon icon="minus-circle" /> Remove as a Member
                            </b-button>
                            <b-button
                                    variant="warning"
                                    size="sm"
                                    @click="transferMember(data.item)"
                                    class="text-white"
                            >
                                <font-awesome-icon icon="users" /> Transfer to other Leader
                            </b-button>
                        </div>
                    </template>
                </b-table>
                <nav v-if="list.total>0">
                    <b-pagination
                            :disabled="loading"
                            @input="getAll"
                            align="right"
                            :total-rows="total_rows"
                            :per-page="per_page"
                            v-model="list.current_page"
                            class="mb-0"
                    >
                    </b-pagination>
                </nav>
            </div>
        </div>
        <b-modal
                v-model="leader_search_modal"
                centered
                size="xl"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Transfer to other Leader</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_leader" />
                    <leaders-table
                            :fields="table_settings.fields"
                            :api_url="table_settings.api_url"
                            :delete_url="table_settings.delete_url"
                            :top_right_btn_redirect="false"
                            @leadersTableDbClick="leadersTableDbClick"
                    />
                </div>
            </div>
        </b-modal>
        <b-modal
                v-model="add_member_modal"
                centered
                size="xl"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Add member</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_leader" />
                    <b-row class="m-0 mb-3">
                        <b-col cols="1" class="text-center p-2 bg-dark text-white d-flex align-items-center justify-content-center">
                            <font-awesome-icon icon="list" class="h2 mb-0" />
                        </b-col>
                        <b-col class="p-3 bg-facebook text-white d-flex align-items-center">
                            <div class="w-100">
                                <select class="form-control" v-model="add_member.member_type_id" required>
                                    <option v-for="member_type in member_types" :value="member_type.id">
                                        {{member_type.name}}
                                    </option>
                                </select>
                                <small class="text-muted text-uppercase font-weight-bold">PLEASE SELECT MEMBER TYPE</small>
                            </div>
                        </b-col>
                    </b-row>
                    <add-members-table
                            :fields="voters_all_table_settings.fields"
                            :api_url="voters_all_table_settings.api_url"
                            :delete_url="voters_all_table_settings.delete_url"
                            :top_right_btn_redirect="false"
                            @addMembersTableDbClick="addMembersTableDbClick"
                            :key="members_table_key"
                    />
                </div>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import {bus} from '@/main.js'
    import SwalMixin from '@/views/mixins/Swal.js'
    import LeadersTable from './LeadersTable.vue';
    import AddMembersTable from './AddMembersTable.vue';
    import Loader from '@/views/helpers/LoaderComponent.vue';
    export default {
        name: 'members-table',
        inheritAttrs: false,
        components: {Loader,LeadersTable,AddMembersTable},
        mixins:[SwalMixin],
        props: {
            hover: {
                type: Boolean,
                default: true
            },
            striped: {
                type: Boolean,
                default: true
            },
            bordered: {
                type: Boolean,
                default: true
            },
            small: {
                type: Boolean,
                default: true
            },
            fixed: {
                type: Boolean,
                default: true
            },
            fields: {
                type: [Array, Object],
                default: () => []
            },
            dark: {
                type: Boolean,
                default: false
            },
            api_url: {
                type: String,
                required: true
            },
            remove_member_url: {
                type: String,
                required: true
            },
            transfer_member_url: {
                type: String,
                required: true
            },
            top_right_btn: {
                type: String,
                default: '',
                required: false
            },
            top_right_btn_redirect: {
                type: Boolean,
                default: true,
                required: false
            },
            id: {
                type: Number,
                required: true
            },
        },
        data(){
            return {
                selected: [],
                list: {
                    data: [],
                    from: 1,
                    to: 0,
                    current_page: 1,
                    total: 0,
                    per_page: 1,
                },
                result:[],
                loading: true,
                searchTimeout: () => ({
                    type: Function,
                    default: () => ({})
                }),
                search_term: '',
                options: [
                    { value: 10, text: 'Display 10 records' },
                    { value: 25, text: 'Display 25 records' },
                    { value: 50, text: 'Display 50 records' },
                    { value: 100, text: 'Display 100 records' }
                ],
                selected_display_no:10,
                leader_search_modal:false,
                is_saving_leader:false,
                selected_voter_id:'',
                table_settings:{
                    api_url:'voters/leaders',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'barangay.name', label:'BARANGAY'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                member:{
                    has_leader:false,
                    leader:{
                        id:-1,
                        name:"Look for leader"
                    },
                    member_type_id:''
                },
                add_member:{
                    has_leader:true,
                    leader:{
                        id:-1,
                    },
                    member_type_id:1
                },
                add_member_modal:false,
                member_types:[],
                voters_all_table_settings:{
                    api_url:'voters/all',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'barangay.name', label:'BARANGAY'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                members_table_key:0,
            }
        },
        computed: {
            table_items: function () {
                return this.list.data;
            },
            per_page: function () {
                return parseInt(this.list.per_page);
            },
            total_rows: function () {
                return parseInt(this.list.total);
            },
            captions: function () {
                return this.fields
            },
            total_selected: function () {
                return parseInt(this.selected.length);
            }
        },
        mounted() {
            this.getAll();
            this.getMemberTypes();
        },
        methods: {
            getAll() {
                this.loading = true;
                this.axios.get(this.api_url + '?id='+this.id+'&page=' + this.list.current_page + '&per_page=' + this.selected_display_no + '&search=' + this.search_term).then(response => {
                    // this.list = (response.data.data) ? response.data.data : response.data;
                    let result=response.data;
                    this.list=result
                    this.loading = false;
                    //console.log(result)
                }).catch(error => console.log(error));
            },
            clickTopRightBtn() {
                //this.$emit('openModal',this.selected);
                bus.$emit('membersTableTopRightBtnClicked');
            },
            rowDbClicked(item) {
                this.$emit('membersTableDbClick',item);
            },
            searching() {
                this.loading = true;
                clearTimeout(this.searchTimeout);
            },
            search() {
                this.searchTimeout = setTimeout(() => {
                    this.list.current_page=1;
                    this.getAll();
                    clearTimeout(this.searchTimeout);
                }, 1000);
            },
            clearSearch(){
                this.search_term=''
                this.getAll();
            },
            changeDisplayNo(){
                this.loading = true;
                this.list.current_page=1;
                this.getAll();
            },
            removeMember(row_id){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.loading=true;
                        this.axios.post(this.remove_member_url,{id:row_id}).then(response => {
                            this.getAll();
                            this.swal_transaction_completed_data.text="Member Successfully Removed";
                            this.swalTransactionCompleted();
                            this.loading=false;
                        }).catch(error => {
                            this.swalRequestError();
                            this.loading=false;
                        });
                    }
                });
            },
            transferMember(row){
                this.member=row;
                this.member.member_id=row.id;
                this.selected_voter_id=row.id;
                this.leader_search_modal=true;
                // this.swalConfirmDelete((data)=>{
                //     if (data.value) {
                //         this.loading=true;
                //         this.axios.post(this.transfer_member_url,{id:row_id}).then(response => {
                //             this.getAll();
                //             this.swal_transaction_completed_data.text="Member Successfully Transferred.";
                //             this.swalTransactionCompleted();
                //             this.loading=false;
                //         }).catch(error => {
                //             this.swalRequestError();
                //             this.loading=false;
                //         });
                //     }
                // });
            },
            leadersTableDbClick(data){
                this.swalConfirmSubmit((confirm)=>{
                    if (confirm.value) {
                        this.is_saving_leader=true;
                        if(data.id==this.id){
                            this.swal_transaction_error_data.text = "Choose another Leader...";
                            this.swalRequestError();

                            this.is_saving_leader=false;
                        }else{
                            this.member.has_leader=true;
                            this.member.leader=data;
                            this.axios.post('voters/validate-leader',{'leader_id':data.id,'member_id':this.selected_voter_id}).then(response => {
                                this.axios.post(this.transfer_member_url,this.member).then(response => {
                                    this.getAll();
                                    this.swal_transaction_completed_data.text="Member Successfully Removed";
                                    this.swalTransactionCompleted();
                                    this.loading=false;
                                }).catch(error => {
                                    this.swalRequestError();
                                    this.loading=false;
                                });
                                this.leader_search_modal=false;
                                this.is_saving_leader=false;
                            }).catch(error => {
                                this.swal_transaction_error_data.text = error.response.data;
                                this.swalRequestError();
                                this.is_saving_leader=false;
                            });
                        }
                    }
                });

            },
            getMemberTypes(){
                this.axios.get('member-types/all').then(response => {
                    //console.log(result)
                    this.member_types = response.data;
                }).catch(error => console.log(error));
            },
            addMembersTableDbClick(data){
                this.is_saving_leader=true;
                if(data.id==this.id){
                    this.swal_transaction_error_data.text = "Leader is NOT VALID...";
                    this.swalRequestError();

                    this.is_saving_leader=false;
                }else{
                    this.axios.post('voters/validate-leader',{'leader_id':this.id,'member_id':data.id}).then(response => {
                        this.leader_search_modal=false;
                        this.add_member.member_id=data.id;
                        this.add_member.member_type_id=(data.member_type_id==0) ? this.add_member.member_type_id : data.member_type_id;
                        this.add_member.leader.id=this.id;

                        this.axios.post('voters/change-leader',this.add_member).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving_leader=false;
                            this.members_table_key++;
                            this.getAll()
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving_leader=false;
                        });
                    }).catch(error => {
                        this.swal_transaction_error_data.text = error.response.data;
                        this.swalRequestError();
                        this.is_saving_leader=false;
                    });
                }
            }
        }
    }
</script>
