<template>
    <div>
        <loader :status="is_saving" />
        <b-row class="m-0 mb-2 rounded border">
            <b-col sm="12" md="3" class="p-2">
                <div class="w-100 border p-2 bg-dark rounded">
                    <img
                            v-if="params.form.image=='' || params.form.image==null"
                            class="w-100 cursor-pointer"
                            src="/img/image-not-found.jpg"
                            @click="upload_modal=true"
                    />
                    <img
                            v-else
                            class="w-100 cursor-pointer"
                            :src="assets_path.MANPOWERS + params.form.image"
                            @click="upload_modal=true"
                    />
                </div>
            </b-col>
            <b-col class="pl-0 pr-2">

                <b-row class="m-0 border p-2 rounded my-2 loan-app-container">
                    <b-col class="p-0">
                        <b-row class="m-0 mb-3">
                            <b-col cols="7" class="p-0">
                                <b-row class="m-0">
                                    <b-col cols="2" class="text-center p-2 bg-dark text-white d-flex align-items-center justify-content-center">
                                        <font-awesome-icon icon="user" class="h2 mb-0" />
                                    </b-col>
                                    <b-col class="p-3 bg-facebook text-white d-flex align-items-center">
                                        <div>
                                            <h4 class="mb-0"><strong>{{voter.name}}</strong> <small><b-badge v-if="voter.member_type == null" variant="danger">Not a member</b-badge>
                                                <b-badge v-else variant="success" @click="changeMemberType()" class="cursor-pointer">{{voter.member_type.name}}</b-badge></small></h4>
                                            <small class="text-muted text-uppercase font-weight-bold">NAME</small>
                                        </div>
                                    </b-col>
                                </b-row>
                            </b-col>
                            <b-col class="p-0 pl-3">
                                <b-row class="m-0 h-100">
                                    <b-col cols="2" class="text-center p-2 bg-dark text-white d-flex align-items-center justify-content-center">
                                        <font-awesome-icon icon="user" class="h2 mb-0" />
                                    </b-col>
                                    <b-col class="p-3 bg-light d-flex align-items-center">
                                        <div>
                                            <h4 class="mb-0 cursor-pointer" @click="changeDetails(voter.leader_id)" v-if="voter.leader_id!=null"><strong>{{voter.leader.name}}</strong></h4>
                                            <h4 class="mb-0" v-else><strong>None</strong></h4>
                                            <small class="text-muted text-uppercase font-weight-bold">LEADER</small>
                                        </div>
                                    </b-col>
                                </b-row>
                            </b-col>
                        </b-row>
                        <b-row class="m-0 p-0" v-if="voter.member_type==null">
                            <b-col class="p-0">
                                <b-button @click="member_modal=true" block variant="success" size="sm" class="mt-3">
                                    <font-awesome-icon icon="check-circle" /> ADD AS A MEMBER
                                </b-button>
                            </b-col>
                        </b-row>
                        <b-row class="m-0 p-0" v-else>
                            <b-col v-if="voter.leader_id!=null">
                                <b-button @click="changeLeader()" block variant="facebook" size="sm" class="mt-3">
                                    <font-awesome-icon icon="check-circle" /> CHANGE LEADER
                                </b-button>
                            </b-col>
                            <b-col v-if="voter.leader_id==null">
                                <b-button @click="changeLeader()" block variant="success" size="sm" class="mt-3">
                                    <font-awesome-icon icon="plus-circle" /> ADD TO A LEADER
                                </b-button>
                            </b-col>
                            <b-col v-if="voter.leader_id!=null">
                                <b-button @click="removeLeader()" block variant="warning" size="sm" class="mt-3 text-white">
                                    <font-awesome-icon icon="times-circle" /> REMOVE MY LEADER
                                </b-button>
                            </b-col>
                            <b-col>
                                <b-button @click="removeMember()" block variant="danger" size="sm" class="mt-3">
                                    <font-awesome-icon icon="times-circle" /> REMOVE AS A MEMBER
                                </b-button>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
            </b-col>
        </b-row>
        <b-form @submit="save">
            <b-tabs content-class="mt-3">
                <b-tab title="Members" active>
                    <members-table
                            :fields="members_table_settings.fields"
                            :api_url="members_table_settings.api_url"
                            :remove_member_url="members_table_settings.remove_member_url"
                            :transfer_member_url="members_table_settings.transfer_member_url"
                            ref="membersTable"
                            :key="membersTableKey"
                            :id="id"
                            @membersTableDbClick="membersTableDbClick"
                    />
                </b-tab>
            </b-tabs>
            <div class="text-right mt-3">
            </div>
        </b-form>
        <b-modal
                v-model="upload_modal"
                centered
                size="md"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card mb-0">
                <div
                        class="card-header bg-facebook text-white d-flex align-items-center justify-content-between"
                >
                    <h6 class="mb-0">
                        <!--<font-awesome-icon icon="upload" />-->
                        Upload a Picture
                    </h6>
                </div>
                <div class="card-body position-relative">
                    <loader :status="is_saving" />
                    <div class="custom-file mb-3">
                        <input
                                type="file"
                                class="custom-file-input"
                                accept="image/*"
                                @change="onChange"
                                required
                        />
                        <label class="custom-file-label" for="validatedCustomFile">Choose file...</label>
                    </div>
                    <div id="preview" class="mb-3">
                        <img v-if="item.imageUrl" :src="item.imageUrl" class="w-100" />
                    </div>
                    <b-progress
                            v-if="hide_submit_btn"
                            :variant="loading_color"
                            :value="percentage"
                            :max="max_upload_percentage"
                            show-progress
                            animated
                            height="1.5rem"
                    >
                        <b-progress-bar :value="percentage" v-if="percentage>0">
                            <!-- <strong v-if="file.error_title!=''">{{ file.name }} - ERROR!</strong> -->
                            <strong>{{ percentage }}%</strong>
                        </b-progress-bar>
                    </b-progress>
                    <b-button v-else @click="upload()" block variant="success" :disabled="item.image==null" size="sm">
                        <font-awesome-icon icon="check-circle" /> UPLOAD
                    </b-button>
                </div>
            </div>
        </b-modal>
        <b-modal
                v-model="member_modal"
                centered
                size="lg"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Add as a Member</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_member" />
                    <b-list-group-item>
                        <b-form-checkbox v-model="member.has_leader" name="check-button" switch >
                            This member has a leader
                        </b-form-checkbox>
                    </b-list-group-item>
                    <b-list-group-item>
                        <b-badge variant="success" v-if="member.has_leader" class="h6 mb-0 cursor-pointer" @click="leader_search_modal=true">{{member.leader.name}}</b-badge><b-badge variant="warning" class="h6 mb-0" v-else>No leader</b-badge>
                    </b-list-group-item>
                    <b-list-group-item>
                        <select class="form-control" v-model="member.member_type_id" required>
                            <option value="">Please Select Type of Member</option>
                            <option v-for="member_type in member_types" :value="member_type.id">
                                {{member_type.name}}
                            </option>
                        </select>
                    </b-list-group-item>
                    <b-button @click="save()" block variant="success" size="sm" class="mt-3" :disabled="isSaveDisabled">
                        <font-awesome-icon icon="check-circle" /> SAVE
                    </b-button>
                </div>
            </div>
        </b-modal>

        <b-modal
                v-model="leader_search_modal"
                centered
                size="xl"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Look for a Leader</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_leader" />
                    <server-side-paginated-table
                            :fields="table_settings.fields"
                            :api_url="table_settings.api_url"
                            :delete_url="table_settings.delete_url"
                            ref="displayTable3"
                            :top_right_btn_redirect="false"
                            @leadersTableDbClick="leadersTableDbClick"
                    />
                </div>
            </div>
        </b-modal>

        <b-modal
                v-model="change_leader_modal"
                centered
                size="lg"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Change Leader</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_member" />
                    <b-list-group-item>
                        <b-form-checkbox v-model="member.has_leader" name="check-button" switch disabled>
                            This member has a leader
                        </b-form-checkbox>
                    </b-list-group-item>
                    <b-list-group-item>
                        <b-badge variant="success" v-if="member.has_leader" class="h6 mb-0 cursor-pointer" @click="leader_search_modal=true">{{member.leader.name}}</b-badge><b-badge variant="warning" class="h6 mb-0" v-else>No leader</b-badge>
                    </b-list-group-item>
                    <b-list-group-item>
                        <select class="form-control" v-model="member.member_type_id" required>
                            <option value="">Please Select Type of Member</option>
                            <option v-for="member_type in member_types" :value="member_type.id">
                                {{member_type.name}}
                            </option>
                        </select>
                    </b-list-group-item>
                    <b-button @click="changeLeaderSave()" block variant="success" size="sm" class="mt-3" :disabled="isSaveDisabled">
                        <font-awesome-icon icon="check-circle" /> SAVE
                    </b-button>
                </div>
            </div>
        </b-modal>
        <b-modal
                v-model="change_type_modal"
                centered
                size="lg"
                body-class="p-0"
                :hide-footer="true"
                :hide-header="true"
        >
            <div class="card bg-light mb-0">
                <div class="card-header bg-dark text-white d-flex"
                >
                    <h6 class="mb-0">Change Member Type</h6>
                </div>
                <div class="card-body bg-white" style="overflow-x: auto;">
                    <loader :status="is_saving_member" />
                    <b-list-group-item>
                        <select class="form-control" v-model="member.member_type_id" required>
                            <option value="">Please Select Type of Member</option>
                            <option v-for="member_type in member_types" :value="member_type.id">
                                {{member_type.name}}
                            </option>
                        </select>
                    </b-list-group-item>
                    <b-button @click="changeMemberTypeSave()" block variant="success" size="sm" class="mt-3" :disabled="isSaveDisabled">
                        <font-awesome-icon icon="check-circle" /> SAVE
                    </b-button>
                </div>
            </div>
        </b-modal>
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import { assets_path } from "@/config";
    import ServerSidePaginatedTable from './LeadersTable.vue';
    import MembersTable from './MembersTable.vue';
    import {bus} from '@/main.js';
    export default {
        mixins:[SwalMixin],
        components: {Loader,ServerSidePaginatedTable,MembersTable},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                params:{
                    form: {
                        id:-1,
                        employee_id:'',
                        first_name: '',
                        middle_name: '',
                        last_name: '',
                        suffix: '',
                        nickname:'',
                        dob: '',
                        job_description_id:'',
                        blood_type_id:'',
                        shirt_size_id:'',
                        marital_status_id:'',
                        gender_id:'',
                        province_id: '',
                        city_id: '',
                        barangay: '',
                        street_address: '',
                        contact_number: '',
                        work_email: '',
                        other_email: '',
                        sss_number:'',
                        philhealth_number:'',
                        pagibig_number:'',
                        date_hired: '',
                        probation_end_date: '',
                        regularization_date:'',
                        image:'',
                        manpower_emergency_contacts:[],
                        manpower_social_media:[],
                        manpower_work_experiences:[],
                        manpower_educations:[],
                        manpower_dependents:[],
                        is_payroll:1,
                        shift_type_id:null,
                        salary_type_id:null,
                        sss:'',
                        hdmf:'',
                        phic:'',

                    },
                    fields:{
                        genders:[],
                        marital_statuses:[],
                        provinces:[],
                        blood_types:[],
                        shirt_sizes:[],
                        preference_categories:[],
                        shift_types:[],
                        salary_types:[]
                    },
                    cash_advances:[],
                    payments:[]
                },
                is_saving_member:false,
                is_saving:false,
                item: {
                    image: null,
                    imageUrl: null,
                    imageName: "",
                },
                assets_path: {},
                upload_modal: false,
                percentage: 0,
                loading_color: "facebook",
                max_upload_percentage: 100,
                hide_submit_btn: false,
                cameraModal: false,
                voter:{
                    id:-1,
                    name:'',
                    member_type_id: '',
                    address: '',
                    barangay: {},
                    children: []
                },
                member_modal:false,
                member:{
                    has_leader:false,
                    leader:{
                        id:-1,
                        name:"Look for leader"
                    },
                    member_type_id:''
                },
                leader_search_modal:false,
                table_settings:{
                    api_url:'voters/leaders',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'barangay.name', label:'BARANGAY'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                member_types:[],
                change_leader_modal:false,
                is_saving_leader:false,
                change_type_modal:false,
                members_table_settings:{
                    api_url:'voters/members',
                    remove_member_url:'voters/remove-member',
                    transfer_member_url:'voters/change-leader',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'barangay.name', label:'BARANGAY'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'},
                        {key: 'action'},
                    ]
                },
                membersTableKey:0
            }
        },
        computed:{
            completeAddress(){
                if(this.params.form.city === null)
                    return ''
                //return this.params.form.street_address+", "+this.params.form.barangay+", "+this.params.form.city.name +", "+this.params.form.province.name;
            },
            isSaveDisabled(){
                if((this.member.has_leader && this.member.leader.id==-1) || this.member.member_type_id=='')
                    return true;
                return false;
            }
        },
        created(){
            this.assets_path = assets_path;
            this.get();
            this.getMemberTypes();
        },
        methods: {
            leadersTableDbClick(data){
                this.is_saving_leader=true;
                if(data.id==this.voter.id){
                    this.swal_transaction_error_data.text = "Leader is NOT VALID...";
                    this.swalRequestError();

                    this.is_saving_leader=false;
                }else{
                    this.axios.post('voters/validate-leader',{'leader_id':data.id,'member_id':this.id}).then(response => {
                        this.leader_search_modal=false;
                        this.member.leader=data;
                        this.is_saving_leader=false;
                    }).catch(error => {
                        this.swal_transaction_error_data.text = error.response.data;
                        this.swalRequestError();
                        this.is_saving_leader=false;
                    });
                }
            },
            membersTableDbClick(data){
                this.changeDetails(data.id)
            },
            getMemberTypes(){
                this.axios.get('member-types/all').then(response => {
                    //console.log(result)
                    this.member_types = response.data;
                }).catch(error => console.log(error));
            },
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('voters/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.voter = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },
            save(){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving_member=true;
                        this.member.member_id=this.voter.id;
                        this.axios.post('voters/add-member',this.member).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving_member=false;
                            this.member_modal=false;
                            this.get();
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving_member=false;
                        });
                    }
                });
            },
            removeLeader(){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving=true;
                        this.axios.post('voters/remove-leader',{id:this.id}).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving=false;
                            this.get();
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving=false;
                        });
                    }
                });
            },
            changeDetails(voter_id){
                this.id=voter_id;
                this.get();
            },
            changeLeader(){
                this.member.has_leader=true;
                this.member.member_type_id=this.voter.member_type_id;
                this.change_leader_modal=true;
            },
            changeMemberType(){
                this.member.member_type_id=this.voter.member_type_id;
                this.change_type_modal=true;
            },
            changeLeaderSave(){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving_member=true;
                        this.member.member_id=this.voter.id;
                        this.axios.post('voters/change-leader',this.member).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving_member=false;
                            this.change_leader_modal=false;
                            this.get();
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving_member=false;
                        });
                    }
                });
            },
            changeMemberTypeSave(){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving_member=true;
                        this.member.member_id=this.voter.id;
                        this.axios.post('voters/change-member-type',this.member).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving_member=false;
                            this.change_type_modal=false;
                            this.get();
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving_member=false;
                        });
                    }
                });
            },

            removeMember(){
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving=true;
                        this.axios.post('voters/remove-member',{id:this.id}).then(response => {
                            this.get();
                            this.swal_transaction_completed_data.text="Member Successfully Removed";
                            this.swalTransactionCompleted();
                            this.is_saving=false;
                        }).catch(error => {
                            this.swalRequestError();
                            this.is_saving=false;
                        });
                    }
                });
            },
            payroll(status){
                event.preventDefault();
                let description = (status == 1) ? 'INCLUDE' : 'EXCLUDE';
                this.swal_confirm_submit_data.title = "Do you want to "+description+" this employee from daily payroll?";
                this.swalConfirmSubmit((data)=>{
                    if (data.value) {
                        this.is_saving=true;
                        let form =  {
                            id:this.params.form.id,
                            is_payroll:status
                        }
                        //this.params.form.province_id = this.params.form.province.id;
                        this.axios.post('maintenance/manpowers/update',form).then(response => {
                            this.swalTransactionCompleted();
                            this.is_saving=false;
                            this.get();
                        }).catch(error => {
                            this.swal_transaction_error_data.text = error.response.data;
                            this.swalRequestError();
                            this.is_saving=false;
                        });
                    }
                });
            },
            dataURItoBlob(dataURI) {
                var byteString;
                if (dataURI.split(",")[0].indexOf("base64") >= 0)
                    byteString = atob(dataURI.split(",")[1]);
                else byteString = unescape(dataURI.split(",")[1]);

                var mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0];
                var ia = new Uint8Array(byteString.length);
                for (var i = 0; i < byteString.length; i++) {
                    ia[i] = byteString.charCodeAt(i);
                }

                return new Blob([ia], { type: mimeString });
            },
            onChange(e) {
                const file = e.target.files[0];
                this.item.image = file;
                this.item.imageUrl = URL.createObjectURL(file);
            },
            upload() {
                let formData = new FormData();
                let file = this.item.image;
                if (this.item.imageName === "") {
                    formData.append("file", file);
                } else {
                    formData.append("file", file, this.item.imageName);
                }
                formData.append("manpower_id", this.params.form.id);
                this.hide_submit_btn = true;
                this.axios
                    .post("maintenance/manpowers/upload-image", formData, {
                        headers: {
                            "Content-Type": "multipart/form-data",
                        },
                        onUploadProgress: (progressEvent) => {
                            this.percentage = parseInt(
                                Math.round((progressEvent.loaded * 100) / progressEvent.total)
                            );
                        },
                    })
                    .then((response) => {
                        this.loading_color = "success";
                        this.swalTransactionCompleted();
                        this.upload_modal = false;
                        console.log(response.data);
                        this.params.form.image = response.data;
                    })
                    .catch((error) => {});
            },
        }
    }
</script>